header {
  display: flex;
  justify-content: space-between;
  margin: 40px 55px;
}

header nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

header a {
  text-decoration: none;
  color: #FF6060;
  font-size: 24px;
  font-weight: 500;
  padding: 0;
}

header nav a {
  text-decoration: none;
  color: #FF6060;
  font-size: 24px;
  font-weight: 500;
  padding: 0 0 0 30px;
}

header nav [aria-current] {
  text-decoration: underline;
}

.logo {
  width: 210px;
  height: auto;
}

/* =========================================================================================== */
@media screen and (max-width: 780px) {

  header {
    margin: 20px 20px;
  }

  header nav a {
    font-size: 12px;
    padding: 0 0 0 15px;
  }

  .logo {
    width: 130px;
    height: auto;
  }
}


